import React, { forwardRef, useEffect, useState } from 'react';
import { elementType, node, object, oneOf, string } from 'prop-types';

const sizes = {
  hero: {
    height: '416px',
    width: '1281px'
  },
  person: {
    height: '250px',
    width: '250px'
  },
  scene: {
    height: '199px',
    width: '401px'
  },
  spotlight: {
    height: '250px',
    width: '250px'
  }
};

const Illustration = forwardRef(({
  children,
  name,
  label,
  style,
  tag: Tag,
  type,
  ...remainingProps
}, ref) => {
  const [image, setImage] = useState();
  useEffect(() => {
    import(`../_imgs/illustrations/${type}/illustration-${type}-${name}.svg`).then(image => setImage(image.default));
  }, [name, type]);

  return (
    <Tag
      alt={Tag === 'img' ? label : undefined}
      aria-label={Tag !== "img" ? label : undefined}
      src={Tag === 'img' ? image : undefined}
      style={
        Tag !== 'img'
        ? {
          backgroundImage: `url(${image})`,
          backgroundRepeat: 'no-repeat',
          height: sizes[type].height,
          width: sizes[type].width,
          ...style
        }
        : style
      }
      role={Tag !== 'img' ? 'img' : undefined}
      ref={ref}
      {...remainingProps}
    >
      {children}
    </Tag>
  );
});

Illustration.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Label
   */
  label: string,

  /**
   * Name of the svg
   */
  name: string.isRequired,

  /**
  * Style
  */
  style: object,

  /**
  * Tag
  */
  tag: elementType,

  /**
  * Type
  */
  type: oneOf([
    'hero',
    'person',
    'scene',
    'spotlight'
  ]).isRequired
};

Illustration.defaultProps = {
  tag: 'div'
};

Illustration.displayName = 'Illustration';

export default Illustration;
