import React, { useState } from 'react';
import Col from '../../col';
import Grid from '../../grid';
import Row from '../../row';
import Typography from '../../typography';
import Illustration from '../../illustration';
import Select from '../../select';
import { hero } from './hero';
import { person } from './person';
import { scene } from './scene';
import { spotlight } from './spotlight';
import { sentenceCase } from "change-case";

const IllustrationExample = () => {
  const [heroSelected, setHeroSelected] = useState(hero[0]);
  const [personSelected, setPersonSelected] = useState(person[0]);
  const [sceneSelected, setSceneSelected] = useState(scene[0]);
  const [spotlightSelected, setSpotlightSelected] = useState(spotlight[0]);

  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">Spotlights</Typography>
          <Typography>
            Spots illustrate a single object or concept. They are the simplest representations and may be highly abstract, but are not icons.
            For a full list of available spotlight images look in the _imgs directory for files the names such as illustration-spot-*.svg.
          </Typography>
          <Select label="Choose Spotlight" onChange={e => setSpotlightSelected(e.target.value)}>
            {
              spotlight.map((s, i) => <option key={i} value={s}>{s}</option>)
            }
          </Select>
        </Col>
      </Row>
      <Row>
        <Col>
          <Illustration label={sentenceCase(spotlightSelected)} name={spotlightSelected} type="spotlight" />
          <div>{`<Illustration label="${sentenceCase(spotlightSelected)}" name="${spotlightSelected}" type="spotlight" />`}</div>
        </Col>
      </Row>
      <Row>
        <Col>
          <Illustration label={sentenceCase(spotlightSelected)} name={spotlightSelected} tag="img" type="spotlight" />
          <div>{`<Illustration label="${sentenceCase(spotlightSelected)}" name=${spotlightSelected} tag="img" type="spotlight" />`}</div>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Person</Typography>
          <Typography>
            People are represented in Scenes and Heroes. Our illustrations feature a diverse array of people to represent a true cross- section of our world.
            For a full list of available people images look in the _imgs directory for files the names such as illustration-person-*.svg.
          </Typography>
          <Select label="Choose Person" onChange={e => setPersonSelected(e.target.value)}>
            {
              person.map((h, i) => <option key={i} value={h}>{h}</option>)
            }
          </Select>
        </Col>
      </Row>
      <Row>
        <Col>
          <Illustration label={sentenceCase(personSelected)} name={personSelected} type="person" />
          <div>{`<Illustration label="${sentenceCase(personSelected)}" name="${personSelected}" type="person" />`}</div>
        </Col>
      </Row>
      <Row>
        <Col>
          <Illustration label={sentenceCase(personSelected)} name={personSelected} tag="img" type="person" />
          <div>{`<Illustration label="${sentenceCase(personSelected)}" name=${personSelected} tag="img" type="person" />`}</div>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Scenes</Typography>
          <Typography>
            Scenes focus on one or two characters and convey an emotion or state. Scenes can float on a page, and are thus do not need a containing object.
            For a full list of available scene images look in the _imgs directory for files the names such as illustration-scene-*.svg.
          </Typography>
          <Select label="Choose Scene" onChange={e => setSceneSelected(e.target.value)}>
            {
              scene.map((s, i) => <option key={i} value={s}>{s}</option>)
            }
          </Select>
        </Col>
      </Row>
      <Row>
        <Col>
          <Illustration label={sentenceCase(sceneSelected)} name={sceneSelected} type="scene" />
          <div>{`<Illustration label="${sentenceCase(sceneSelected)}" name="${sceneSelected}" type="scene" />`}</div>
        </Col>
      </Row>
      <Row>
        <Col>
          <Illustration label={sentenceCase(sceneSelected)} name={sceneSelected} tag="img" type="scene" />
          <div>{`<Illustration label="${sentenceCase(sceneSelected)}" name=${sceneSelected} tag="img" type="scene" />`}</div>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Heroes</Typography>
          <Typography>
            Hero illustrations are complex, full-width graphics which tell stories. These illustrations can include more objects and context and fewer abstract representations.
            For a full list of available hero images look in the _imgs directory for files the names such as illustration-hero-*.svg.
          </Typography>
          <Select label="Choose Hero" onChange={e => setHeroSelected(e.target.value)}>
            {
              hero.map((h, i) => <option key={i} value={h}>{h}</option>)
            }
          </Select>
        </Col>
      </Row>
      <Row>
        <Col>
          <Illustration label={sentenceCase(heroSelected)} name={heroSelected} type="hero" />
          <div>{`<Illustration label="${sentenceCase(heroSelected)}" name="${heroSelected}" type="hero" />`}</div>
        </Col>
      </Row>
      <Row>
        <Col>
          <Illustration label={sentenceCase(heroSelected)} name={heroSelected} tag="img" type="hero" />
          <div>{`<Illustration label="${sentenceCase(heroSelected)}" name=${heroSelected} tag="img" type="hero" />`}</div>
        </Col>
      </Row>
    </Grid>
  );
};

export default IllustrationExample;
